import React, {useState} from 'react';
import './style.scss';
import {connect} from "../../components"
import {toast} from "../../utils"
// let canUseContractRaw = false;
// window.demos.canIUse('contractRaw').then((data) => {
//     canUseContractRaw = data;
// });

function Home({address, balance, utxoLength, utxoSummary, utxoData, reloadData}) {
    const handleSummary = async () => {
        const ret = await window.demos.summary(utxoData);
        if(ret.transactionHash) {
            // toast('汇总成功');
            reloadData(address);
        }else {
            // toast('汇总失败');
        }
    };
    return (
        <div className='p-home'>
            <div className='home-top' />
            <div className='home-container'>
                <div className='btn-top'>DEMOS</div>
                <ul className='list'>
                    <li>隐私</li>
                    <li>安全</li>
                    <li>便捷</li>
                </ul>
                <div className='card'>
                    <p>{balance/1e8}</p>
                    <i>{address}</i>
                </div>
                <div className='card row'>
                    <p>{utxoLength}</p>
                    <div className='utxo'>UTXO</div>
                </div>
                <div className='card'>
                    <p>{utxoSummary/1e8}</p>
                    <i>本次汇总金额</i>
                </div>
            </div>
            <div className='btn' onClick={handleSummary}>汇总</div>
        </div>
    )
}

export default connect()(Home);
